.table {
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.3);
    border-top: 4px solid #00B676;
    &-container {
        @include media-breakpoint-down(md) {
            overflow-x: auto;
            width: calc(100% + 48px);
            padding: 8px 24px;
            margin: 0 -24px;
        }
    }
    th {
        pointer-events: auto;
    }
    tr {
        pointer-events: auto;
    }
    td {
        border-top-color: rgba(222, 226, 230, 0.75);
    }
    thead {
        pointer-events: auto;
    }
    thead tr {
        pointer-events: auto;
    }
    tbody tr {
        transition: background-color 200ms linear;
        &:hover {
            background-color: rgba(222, 226, 230, 0.25);
        }
    }
    tfoot {
        pointer-events: auto;
    }
    tfoot tr {
        pointer-events: auto;
    }
    tfoot td {
        pointer-events: auto;
    }
}



