
.page {
    min-height: 100vh;
    display: flex;
    flex-flow: column nowrap;

    &-header {
        padding: 24px;
        background-color: $accentBackground;
        box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.3);
    }
    &-headerLogo {
        height: 68px;

        img {
            max-width: 100%;
            max-height: 100%;
        }
    }
    &-headerLogoLink {
        pointer-events: auto;
    }
    &-body {
        flex: 1 1 auto;
        display: flex;
        flex-flow: row nowrap;
        margin: 0;
        padding: 0;

        @include media-breakpoint-down(md) {
            flex-flow: column nowrap;
        }
    }
    &-leftPanel {
        flex: 0 0 auto;
        padding: 24px;
        width: 25%;
        max-width: 350px;

        @include media-breakpoint-down(md) {
            width: auto;
            max-width: auto;
        }
    }
    &-mainPanel {
        flex: 1 1 auto;
        padding: 24px;
    }
}



