@import '../../assets/sass/references';
.redemptions {
    h2 {
        margin-top: 18px;
        font-size: 28px;
    }
    h3 {
        font-size: 18px;
    }
    hr {
        margin: 36px 0;
        border-bottom-color: rgba(222, 226, 230, 0.75);
    }
    &-tag {
        display: inline-block;
        font-size: 12px;
        margin-bottom: 8px;
        color: #00B676;
        text-transform: uppercase;
        padding: 8px 16px;
        box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.3);
        & + .redemptions-tag {
            margin-left: 16px;
        }
    }
    &-footer {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        align-items: center;
    }
    &-itemCount {

    }
    &-pagination {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;

        p {
            margin: 0 16px;
        }
    }
}



