@import '../../assets/sass/references';

.tenantDetails {
    padding: 24px;
    box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.3);
    border-top: 4px solid #00B676;

    &-error {
        pointer-events: auto;
    }

    &-body {
        pointer-events: auto;
    }

    h1 {
        font-size: 28px;
    }

    h2 {
        font-size: 16px;
        margin-bottom: 8px;
        color: #999;
    }

    dl {
        pointer-events: auto;
    }

    dt {
        pointer-events: auto;
    }

    dd {
        pointer-events: auto;
    }

    dd + dd {
        margin-top: -0.5rem;
    }
}



